<template>
  <div id="app">
    <HeaderBar/>
    <main>
      <h1 id="hello">모든 것이 완벽합니다 :)</h1>
      <ul>
        <li v-for="test in tests" v-bind:key="test.key" class="real-item">
          <div class="item_container">
            <div class="danger" v-bind:class="{ 'is_danger' : test.isDanger }"><p draggable="false">위험</p></div>
            <div class="light" v-bind:class="{ 'green' : (test.status && !(test.isDanger)), 'red' : (!(test.status) && !(test.isDanger)) }"></div>
            <div class="content">
              <div class="name">
                <p class="name">이름 : <input v-model="test.name" @input="changeName(test)"></p>
              </div>
              <hr>
              <div class="stat">
                <p class="stat status_on" v-if="test.status && !(test.isDanger)">켜졌습니다</p>
                <p class="stat status_off" v-if="!test.status && !(test.isDanger)">꺼졌습니다</p>
                <p class="stat" v-if="test.callNum != null">찾은 횟수 : {{test.callNum}}</p>
                <p class="stat" v-if="test.callNum == null">찾은 횟수 정보 없음</p>
                <p class="stat" v-if="(test.connectionTime != null && test.connectionTime != -1)">최근 연결 : 
                  <!-- <p v-if="test.connectionTime == 0">정보 없음</p> -->
                  {{getWorldTime(+9)}}
                </p>
                <p class="stat" v-if="(test.connectionTime == null || test.connectionTime == -1)">연결 정보 없음</p>
                <p class="stat" v-if="test.ip != null && test.ip != ''">IP : {{test.ip}}</p>
                <p class="stat" v-if="test.ip == null || test.ip == ''">IP 정보 없음</p>
                <button class="stat" @click="test.isCalled = true; changeIsCalled(test)">호출 : {{test.isCalled}}</button>
              </div>
            </div>
          </div>
        </li>
        <li class="plus-item" @click="showModal = true">
          <img src="./assets/ic_add_black_24px.svg" alt="plus" draggable="false">
          <h1 draggable="false">기기 추가</h1>
        </li>
      </ul>
      <modal v-if="showModal" v-model="newItem" @close="showModal = false; addItem()" @forceClose="showModal = false"/>
    </main>
  </div>
</template>

<script>
import Firebase,{ firestore } from 'firebase'
import HeaderBar from './components/HeaderBar'
import modal from './components/modal'

let config = {
  apiKey: "AIzaSyDqpN9M0OWICMxWLiervQkhe5KjlAvmGwc",
  authDomain: "white-stick.firebaseapp.com",
  databaseURL: "https://white-stick.firebaseio.com",
  projectId: "white-stick",
  storageBucket: "white-stick.appspot.com",
  messagingSenderId: "761920402571"
}

let app = Firebase.initializeApp(config)
let db = app.database()

let whitestick = db.ref('data')

export default {
  name: 'App',
  components: { HeaderBar, modal },
  firebase: function () {
    return {
      tests: whitestick
    }
  },
  data () {
    return {
      newItem: {
        name: '',
        isDanger: false,
        code : 0
      },
      showModal: false
    }
  },
  methods: {
    changeName(item) {
      const copy = {...item}

      delete copy['.key']
      this.$firebaseRefs.tests.child(item['.key']).set(copy)
    },
    addItem() {
      // console.log(this.newItem)
      this.newItem.callNum = 0
      this.newItem.connectionTime = -1
      this.newItem.ip = "125:0:0:1"
      this.newItem.isCalled = false
      this.newItem.status = false

      // whitestick.push(this.newItem)
      this.$firebaseRefs.tests.child(this.newItem.code).set(this.newItem)

      this.newItem.name = ''
      this.newItem.isDanger = false
      this.newItem.code = 0
    }, changeIsCalled(item) {
      const copy = {...item}

      delete copy['.key']
      this.$firebaseRefs.tests.child(item['.key']).set(copy)
    }, getWorldTime (tzOffset) { // 24시간제
      var now = new Date();
      var tz = now.getTime() + (now.getTimezoneOffset() * 60000) + (tzOffset * 3600000);
      
      now.setTime(tz);
      
      var leadingZeros = (n, digits) => {
        var zero = '';

        n = n.toString();

        if (n.length < digits) {
          for (var i = 0; i < digits - n.length; i++)
            zero += '0';

          return zero + n;
        }
      }

      var s =
        leadingZeros(now.getMonth() + 1, 2) + '월 ' +
        leadingZeros(now.getDate(), 2) + '일 ' +

        leadingZeros(now.getHours(), 2) + '시 ' +
        leadingZeros(now.getMinutes(), 2) + '분'

      return s;
    }        
  }
}
</script>

<style scoped>
@import url('https://fonts.googleapis.com/css?family=Nanum+Gothic');

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;

  font-family: 'Nanum Gothic', sans-serif;
}

main {
  display: flex;
  flex-flow: column;

  justify-content: center;
  align-items: center;
}

ul {
  display: grid;

  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 38em;

  margin: 1em;
}

li {
  display: block;

  box-shadow: 0 7px 28px rgba(0,0,0,0.22), 0 5px 10px rgba(0,0,0,0.18);

  height: auto;
  margin: 3em 2em;
}

.item_container {
  display: flex;
  flex-flow: column;

  position: relative;

  height: 100%;

  justify-content: center;
  align-items: center;
}

li.plus-item {
  display: flex;
  flex-flow: column;

  justify-content: center;
  align-items: center;

  cursor: pointer;

  border: 2px dashed #2a2a2a;

  height: auto;

  box-sizing: border-box;

  transition: 300ms;
}

li.plus-item:hover {
  background-color: #eaeaea;
}

li.plus-item:active {
  background-color: #cacaca;
}

li.plus-item img {
  width: 40%;
  height: 40%;
}

li.plus-item h1 {
  margin: 0.3em;
  font-size: 1.6em;
}

h1#hello {
  font-size: 3em;
  margin: 1.5em;
  margin-top: 2.3em;

  width:  14em;

  color: #5a5a5a;

  animation-duration: 2.4s;
  animation-name: glow;
  animation-iteration-count: infinite;

  padding-bottom: 0.4em;
  border-bottom: 2px solid #5a5a5a;
}

.content {
  display: flex;
  flex-flow: column;

  justify-content: center;
  align-items: center;

  font-size: 1.3em;
}

.danger {
  display: flex;
  flex-flow: column;

  position: absolute;
  
  top: 0.7em;
  left: 0.8em;

  width: 3em;
  height: 2em;
  border-radius: 3em;

  color: #8a8a8a;

  border: 1px solid #8a8a8a;
  background-color: white;

  justify-content: center;
  align-items: center;
}

.danger p {
  position: relative;
  top: 2px;
  width: 100%;
  height: 100%;
  margin: 0;
  
}

.danger.is_danger {
  color: white;

  border: 1px solid #ff6b4e;
  background-color: #ff6b4e;
}

.danger.not_danger {
  color: white;

  border: 1px solid #66bb6a;
  background-color: #66bb6a;
}

.light {
  display: block;
  position: absolute;
  
  top: 0.7em;
  left: calc(100% - 1.8em);

  width: 1em;
  height: 1em;
  border-radius: 0.5em;
  background-color: #8a8a8a;
}

.light.green {
  background-color: #66bb6a;
}

.light.red {
  background-color: #ff6b4e;
}

.content input {
  font-size: 1em;
  width: 7em;
}

.content hr {
  width: 10em;
  height: 1px;

  margin: 0;

  background-color: black;
}

.content > .name {
  margin-left: 2em;
  margin-right: 3em;
}

.content p.name input {
    font-size: 1em;
    width: 7em;
    height: 1.6em;
    border: none;
    border: 1px solid #6a6a6a;
    /* text-align: center; */
    border-radius: 0.4em;
    /* border-color: #ffffff; */
    padding-left: 0.3em;
    outline: none;
}

.content > p.stat {
  display: flex;
  flex-flow: column;

  justify-content: center;
  align-items: center;

  margin: 3em 1.2em;
}

.content button.stat {
  font-size: 1em;
  width: 8em;
  margin-top: 0.6em;
  border: 1px solid #2a2a2a;
  border-radius: 1em;
  background-color: white;
  transition: 300ms;
}

.content button.stat:hover {
  background-color: #eaeaea;
}

.content button.stat:active {
  background-color: #cacaca;
}

.stat.status_on {
  color: #66bb6a;
}

.stat.status_off {
  color: #ff6b4e;
}

li p {
  margin: 1.4em;
}

li p.name {
  font-size: 1.2em;
}

li p.stat {
  margin: 0.6em;
}

@keyframes glow {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.35;
  }
  100% {
    opacity: 1;
  }
}
</style>



